





































import Vue from 'vue';
import createGame from '@/actions/createGame';
import Component from 'vue-class-component';

@Component
export default class GameCreate extends Vue {
  error = '';

  userName = '';

  async handleSubmit() {
    await createGame(this.userName);

    this.$gtag.event('game', {
      event_category: 'create',
    });
  }
}
